<template>
  <div class="hello">
    <!-- START BREADCRUMB -->
    <ul class="breadcrumb" style="text-align:left;">
      <li><a href="#"></a></li>
      <li><a href="#"></a></li>
      <li class="active"></li>
    </ul>
    <!-- END BREADCRUMB -->

    <!-- PAGE CONTENT WRAPPER -->
    <div class="page-content-wrap">

      <div class="row">
        <div class="col-md-12">

          <form class="form-horizontal" @submit.prevent="validateBeforeSubmit">
            <div class="panel panel-default">
              <div class="panel-heading">
                <h3 class="panel-title"><strong></strong> </h3>
                <ul class="panel-controls">
                  <li><a href="#" class="panel-remove"><span class="fa fa-times"></span></a></li>
                </ul>
              </div>
              <div class="panel-body">
                <p style="text-align:left;">1.2.</p>
              </div>

              <div class="panel-body">

                <div class="row">

                  <div class="col-md-6">

                    <div class="form-group">
                      <label class="col-md-3 control-label"></label>
                      <div class="col-md-9">
                        <div class="input-group" :class="{'has-error':errors.has('')}">
                          <span class="input-group-addon"><span class="fa fa-pencil"></span></span>
                          <input type="text" name="" class="form-control" v-validate="'required'"
                                 :class="{'input': true, 'is-danger': errors.has('') }"
                                 v-model.value=emailStyle.regSubject />
                        </div>
                        <i v-show="errors.has('')" class="fa fa-warning alarm"></i>
                        <span v-show="errors.has('')" class="help is-danger alarm ">{{ errors.first('') }}</span>
                        <span class="help-block">(username)</span>
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-md-3 control-label"></label>
                      <div class="col-md-9 col-xs-12">
                        <div :class="{'has-error':errors.has('')}">
                        <textarea rows="7" type="text" name="" class="form-control" v-validate="'required'"
                                  :class="{'input': true, 'is-danger': errors.has('') }"
                                  v-model.value=emailStyle.regText ></textarea>
                        </div>
                        <i v-show="errors.has('')" class="fa fa-warning alarm"></i>
                        <span v-show="errors.has('')" class="help is-danger alarm">{{ errors.first('') }}</span>
                        <span class="help-block">(username)</span>
                      </div>
                    </div>

                  </div>

                  <div class="col-md-6">

                    <div class="form-group">
                      <label class="col-md-3 control-label"></label>
                      <div class="col-md-9">
                        <div class="input-group" :class="{'has-error':errors.has('')}">
                          <span class="input-group-addon"><span class="fa fa-pencil"></span></span>
                          <input type="text" name="" class="form-control" v-validate="'required'"
                                 :class="{'input': true, 'is-danger': errors.has('') }"
                                 v-model.value=emailStyle.alarmSubject />
                        </div>
                        <i v-show="errors.has('')" class="fa fa-warning alarm"></i>
                        <span v-show="errors.has('')" class="help is-danger alarm">{{ errors.first('') }}</span>
                        <span class="help-block">(username)</span>
                      </div>
                    </div>

                    <div class="form-group">
                      <label class="col-md-3 control-label"></label>
                      <div class="col-md-9 col-xs-12">
                        <div :class="{'has-error':errors.has('')}">
                        <textarea rows="7" type="text" name="" class="form-control" v-validate="'required'"
                                  :class="{'input': true, 'is-danger': errors.has('') }"
                                  v-model.value=emailStyle.alarmText ></textarea>
                        </div>
                        <i v-show="errors.has('')" class="fa fa-warning alarm"></i>
                        <span v-show="errors.has('')" class="help is-danger alarm">{{ errors.first('') }}</span>
                        <span class="help-block">(username)</span>
                      </div>
                    </div>

                  </div>

                  <div class="form-group">
                    <label class="col-md-3 col-xs-12 control-label"> </label>
                    <div class="col-md-6 col-xs-12">
                      <div class="input-group">
                      </div>
                    </div>
                  </div>

                  <div class="form-group" >
                    <label class="col-md-3 col-xs-12 control-label">:</label>
                    <div class="col-md-6 col-xs-12">
                      <div class="input-group" :class="{'has-error':errors.has('')}">
                        <span class="input-group-addon"><span class="fa fa-pencil"></span></span>
                        <input type="text" name="" class="form-control" v-validate="'required|email'"
                               :class="{'input': true, 'is-danger': errors.has('') }"
                               v-model.value=emailStyle.emailNumber />
                      </div>
                      <i v-show="errors.has('')" class="fa fa-warning alarm"></i>
                      <span v-show="errors.has('')" class="help is-danger alarm">{{ errors.first('') }}</span>
                      <span class="help-block"></span>
                    </div>
                  </div>

                  <div class="form-group">
                    <label class="col-md-3 col-xs-12 control-label">:</label>
                    <div class="col-md-6 col-xs-12">
                      <div class="input-group" :class="{'has-error':errors.has('')}" >
                        <span class="input-group-addon"><span class="fa fa-pencil"></span></span>
                        <input type="text" name="" class="form-control" v-validate="'required'"
                               :class="{'input': true, 'is-danger': errors.has('') }"
                               v-model.value=emailStyle.emailPasswd />
                      </div>
                      <i v-show="errors.has('')" class="fa fa-warning alarm"></i>
                      <span v-show="errors.has('')" class="help is-danger alarm">{{ errors.first('') }}</span>
                      <span class="help-block"></span>
                    </div>
                  </div>

                </div>

              </div>
              <div class="panel-footer">
                <button class="btn btn-success btn-lg col-md-offset-6"></button>
              </div>

            </div>
          </form>

        </div>
      </div>

    </div>
    <!-- END PAGE CONTENT WRAPPER -->
  </div>
</template>

<script>
  import Vue from 'vue'
  import VueResource from 'vue-resource'
  import VeeValidate, { Validator } from 'vee-validate'
  import messages from '../assets/js/zh_CN'
  Validator.updateDictionary({
    zh_CN: {
      messages
    }
  })
  const config = {
    errorBagName: 'errors', // change if property conflicts.
    delay: 0,
    locale: 'zh_CN',
    messages: null,
    strict: true
  }
  Vue.use(VueResource)
  Vue.use(VeeValidate, config)

  export default {
    data () {
      return {
        emailStyle: {
          regSubject: '',
          regText: '',
          alarmSubject: '',
          alarmText: '',
          emailNumber: '',
          emailPasswd: ''
        },
        emailUrl: 'http://localhost:3000/emailStyle'
      }
    },
    mounted: function () {
      this.getMailStyle()
    },
    methods: {
      getMailStyle: function () {
        var resource = this.$resource(this.emailUrl)
        resource.get()
          .then((response) => {
            this.emailStyle = response.body
          })
      },
      saveMailStyle: function () {
        var Params = {regSubject: this.emailStyle.regSubject,
          regText: this.emailStyle.regText,
          alarmSubject: this.emailStyle.alarmSubject,
          alarmText: this.emailStyle.alarmText,
          emailNumber: this.emailStyle.emailNumber,
          emailPasswd: this.emailStyle.emailPasswd
        }
        var resource = this.$resource(this.emailUrl)
        resource.save(this.emailUrl, Params)
          .then((response) => {
            this.getMailStyle()
            this.$message({
              message: '',
              type: 'success'
            })
          })
      },
      validateBeforeSubmit (e) {
        this.$validator.validateAll()
        if (!this.errors.any()) {
          this.submitForm()
        }
      },
      submitForm () {
        alert('11212122')
        this.saveMailStyle()
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.alarm{
  color: red
}
</style>



